import { useState, useEffect, useRef } from "react";
import { Compass, Home } from "lucide-react";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

export default function NotFoundPage() {
  const [animate, setAnimate] = useState(false);
  const [glitchActive, setGlitchActive] = useState(false);
  const [digitTime, setDigitTime] = useState(0);
  const containerRef = useRef(null);
  const particlesRef = useRef([]);

  // Initialize random particle positions
  useEffect(() => {
    particlesRef.current = Array(40)
      .fill()
      .map(() => ({
        x: Math.random() * 100,
        y: Math.random() * 100,
        size: Math.random() * 6 + 2,
        speedX: (Math.random() - 0.5) * 0.3,
        speedY: (Math.random() - 0.5) * 0.3,
        hue: Math.floor(Math.random() * 60) + 250,
        opacity: Math.random() * 0.7 + 0.3,
      }));
  }, []);

  useEffect(() => {
    // Initial animation
    setAnimate(true);
    setTimeout(() => setAnimate(false), 1000);

    // Random glitch effect
    const glitchInterval = setInterval(() => {
      setGlitchActive(true);
      setTimeout(() => setGlitchActive(false), 200);
    }, 3000);

    // Digital time counter for 404
    const digitInterval = setInterval(() => {
      setDigitTime((prev) => (prev + 1) % 100);
    }, 100);

    return () => {
      clearInterval(glitchInterval);
      clearInterval(digitInterval);
    };
  }, []);

  const triggerAnimation = () => {
    setAnimate(true);
    setGlitchActive(true);
    setTimeout(() => {
      setAnimate(false);
      setGlitchActive(false);
    }, 800);
  };

  return (
    <>
      <Helmet>
        <title>404 Not Found</title>
      </Helmet>
      <div
        className="bg-gradient-to-br from-indigo-900 via-purple-900 to-black min-h-screen flex items-center justify-center overflow-hidden"
        ref={containerRef}
      >
        {/* Animated gradient background */}
        <div className="absolute inset-0 opacity-30">
          <div className="absolute inset-0 bg-gradient-to-r from-blue-500 to-purple-600 animate-pulse"></div>
          <div className="absolute inset-0 bg-[radial-gradient(circle,transparent_20%,#120b29_80%)]"></div>
        </div>

        {/* Digital rain effect (Matrix-like) */}
        <div className="absolute inset-0 overflow-hidden opacity-20 pointer-events-none">
          {[...Array(15)].map((_, i) => (
            <div
              key={`rain-${i}`}
              className="absolute top-0 text-blue-300 animate-digital-rain"
              style={{
                left: `${(i / 15) * 100}%`,
                fontSize: `${Math.random() * 10 + 10}px`,
                opacity: Math.random() * 0.5 + 0.2,
                animationDuration: `${Math.random() * 10 + 5}s`,
                animationDelay: `${Math.random() * 2}s`,
              }}
            >
              {"۱۰۱۰۴۰۴۰۱۱۰".split("").map((char, j) => (
                <div
                  key={j}
                  style={{
                    animationDelay: `${j * 0.1}s`,
                    opacity: Math.random() * 0.9 + 0.1,
                  }}
                >
                  {char}
                </div>
              ))}
            </div>
          ))}
        </div>

        {/* Grid lines with pulse */}
        <div className="absolute inset-0 bg-[linear-gradient(rgba(66,71,112,0.3)_1px,transparent_1px),linear-gradient(90deg,rgba(66,71,112,0.3)_1px,transparent_1px)] bg-[length:40px_40px] opacity-20 animate-grid-pulse"></div>

        {/* Floating particles with trails */}
        <div className="absolute inset-0 overflow-hidden">
          {particlesRef.current.map((particle, i) => (
            <div
              key={`particle-${i}`}
              className="absolute rounded-full particle-animation"
              style={{
                width: `${particle.size}px`,
                height: `${particle.size}px`,
                left: `${particle.x}%`,
                top: `${particle.y}%`,
                background: `hsla(${particle.hue}, 70%, 60%, ${particle.opacity})`,
                boxShadow: `0 0 ${particle.size * 3}px hsla(${
                  particle.hue
                }, 70%, 60%, 0.6)`,
                filter: "blur(1px)",
                animationDuration: `${Math.random() * 20 + 15}s`,
                animationDelay: `${Math.random() * 5}s`,
              }}
            />
          ))}
        </div>

        {/* Scanner effect */}
        <div className="absolute inset-0 overflow-hidden">
          <div className="absolute top-0 left-0 w-full h-2 bg-gradient-to-r from-transparent via-cyan-400 to-transparent opacity-30 animate-scanner"></div>
        </div>

        {/* Main content */}
        <div
          className={`relative z-10 text-center max-w-3xl mx-4 transition-all duration-700 ease-out ${
            animate ? "scale-105" : "scale-100"
          } ${glitchActive ? "glitch-effect" : ""}`}
        >
          {/* 404 Number with digital effect */}
          <div className="relative mb-6">
            <div className="absolute -inset-1 blur-lg bg-gradient-to-r from-purple-600 to-pink-600 opacity-30 animate-pulse"></div>
            <div className="relative flex justify-center items-center overflow-hidden h-40">
              {/* Digital counter effect */}
              <div className="flex items-center justify-center">
                <div className="digital-cell">
                  <div className="digital-number">
                    {digitTime % 10 === 4
                      ? "4"
                      : Math.floor(Math.random() * 10)}
                  </div>
                  <div className="digital-fixed">4</div>
                </div>
                <div className="digital-cell">
                  <div className="digital-number">
                    {digitTime % 10 === 0
                      ? "0"
                      : Math.floor(Math.random() * 10)}
                  </div>
                  <div className="digital-fixed">0</div>
                </div>
                <div className="digital-cell">
                  <div className="digital-number">
                    {digitTime % 10 === 4
                      ? "4"
                      : Math.floor(Math.random() * 10)}
                  </div>
                  <div className="digital-fixed">4</div>
                </div>
              </div>
            </div>
          </div>

          {/* Creative Farsi Text */}
          <h1
            className={`text-6xl mb-4 font-bold text-transparent bg-clip-text bg-gradient-to-r from-violet-400 to-fuchsia-500 font-[Vazirmatn] ${
              glitchActive ? "translate-x-1 skew-x-2" : ""
            } transition-all duration-200 tracking-widest reveal-text`}
          >
            گم‌گشته در فضای مجازی
          </h1>

          <div className="max-w-xl mx-auto text-lg mb-6 text-blue-100 font-[Vazirmatn] leading-relaxed space-y-4 fade-in-up">
            <p className="drop-shadow-glow mb-2">
              در هزارتوی این دنیای دیجیتال، به مسیری رسیده‌اید که وجود ندارد.
              صفحه‌ای که به دنبالش هستید به اعماق تاریکی فرو رفته است.
            </p>
            <p className="text-purple-300 italic font-light text-base">
              مانند ققنوسی که در خاکستر خود گم شده، این مسیر نیز از نقشه‌ها محو
              گشته است. اما نگران نباشید، راه بازگشت همیشه روشن است...
            </p>
          </div>

          <div className="mt-12 flex flex-wrap justify-center gap-6 fade-in-up animation-delay-500">
            <button
              className="group bg-gradient-to-r from-indigo-600 to-purple-700 text-white px-8 py-4 rounded-lg font-medium flex items-center gap-3 hover:from-indigo-700 hover:to-purple-800 transition-all duration-300 shadow-lg shadow-indigo-800/50 hover:shadow-indigo-700/60"
              onClick={triggerAnimation}
            >
              <div className="relative w-6 h-6 overflow-hidden">
                <Compass className="w-6 h-6 absolute group-hover:-translate-y-8 transition-transform duration-300" />
                <Compass className="w-6 h-6 absolute translate-y-8 group-hover:translate-y-0 transition-transform duration-300" />
              </div>
              <span className="font-[Vazirmatn] text-lg relative overflow-hidden">
                <Link to={"/"}>
                  <span className="inline-block group-hover:-translate-y-8 transition-transform duration-300">
                    یافتن مسیر
                  </span>
                </Link>
                <Link to={`/`}>
                  <span className="absolute top-0 left-0 translate-y-8 group-hover:translate-y-0 transition-transform duration-300 whitespace-nowrap">
                    تلاش مجدد
                  </span>
                </Link>
              </span>
            </button>

            <button
              className="group relative px-8 py-4 rounded-lg font-medium flex items-center gap-3 transition-all duration-300 overflow-hidden"
              onClick={triggerAnimation}
            >
              <span className="absolute inset-0 bg-transparent border border-purple-400 rounded-lg group-hover:scale-95 transition-transform duration-500"></span>
              <span className="absolute inset-0 bg-gradient-to-r from-transparent to-purple-600/10 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></span>
              <div className="relative w-6 h-6 overflow-hidden">
                <Home className="w-6 h-6 text-purple-300 absolute group-hover:-translate-y-8 transition-transform duration-300" />
                <Home className="w-6 h-6 text-fuchsia-400 absolute translate-y-8 group-hover:translate-y-0 transition-transform duration-300" />
              </div>
              <span className="font-[Vazirmatn] text-lg relative overflow-hidden text-purple-200">
                <span className="inline-block group-hover:-translate-y-8 transition-transform duration-300">
                  بازگشت به خانه
                </span>
                <span className="absolute top-0 left-0 translate-y-8 group-hover:translate-y-0 transition-transform duration-300 whitespace-nowrap text-fuchsia-300">
                  آغاز دوباره
                </span>
              </span>
            </button>
          </div>

          {/* Circular pulse effects */}
          <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full pointer-events-none">
            <div className="absolute inset-0 circular-pulse"></div>
            <div className="absolute inset-0 circular-pulse delay-1000"></div>
            <div className="absolute inset-0 circular-pulse delay-2000"></div>
          </div>

          {/* Background blobs */}
          <div className="absolute -top-32 -right-32 w-64 h-64 bg-gradient-to-r from-purple-700/20 to-fuchsia-700/20 rounded-full blur-3xl animate-blob"></div>
          <div className="absolute -bottom-48 -left-48 w-80 h-80 bg-gradient-to-r from-blue-700/20 to-indigo-700/20 rounded-full blur-3xl animate-blob animation-delay-2000"></div>
        </div>

        {/* Add styles for custom animations */}
        <style jsx>{`
          @keyframes digitalRain {
            0% {
              transform: translateY(-100%);
            }
            100% {
              transform: translateY(100vh);
            }
          }
          .animate-digital-rain {
            animation: digitalRain linear infinite;
          }

          @keyframes scannerMove {
            0% {
              transform: translateY(-100%);
            }
            100% {
              transform: translateY(100vh);
            }
          }
          .animate-scanner {
            animation: scannerMove 4s ease-in-out infinite;
          }

          @keyframes gridPulse {
            0%,
            100% {
              opacity: 0.2;
            }
            50% {
              opacity: 0.3;
            }
          }
          .animate-grid-pulse {
            animation: gridPulse 4s ease-in-out infinite;
          }

          @keyframes blob {
            0% {
              transform: scale(1) translate(0px, 0px);
            }
            33% {
              transform: scale(1.1) translate(-50px, 30px);
            }
            66% {
              transform: scale(0.9) translate(20px, -20px);
            }
            100% {
              transform: scale(1) translate(0px, 0px);
            }
          }
          .animate-blob {
            animation: blob 15s ease-in-out infinite;
          }

          @keyframes particleFloat {
            0% {
              transform: translate(0, 0);
            }
            25% {
              transform: translate(20px, -15px);
            }
            50% {
              transform: translate(-10px, 20px);
            }
            75% {
              transform: translate(-15px, -10px);
            }
            100% {
              transform: translate(0, 0);
            }
          }
          .particle-animation {
            animation: particleFloat ease-in-out infinite;
          }

          @keyframes glitch {
            0% {
              transform: translate(0);
            }
            20% {
              transform: translate(-5px, 5px);
            }
            40% {
              transform: translate(-5px, -5px);
            }
            60% {
              transform: translate(5px, 5px);
            }
            80% {
              transform: translate(5px, -5px);
            }
            100% {
              transform: translate(0);
            }
          }
          .glitch-effect {
            animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both
              infinite;
          }

          @keyframes fadeInUp {
            from {
              opacity: 0;
              transform: translateY(20px);
            }
            to {
              opacity: 1;
              transform: translateY(0);
            }
          }
          .fade-in-up {
            animation: fadeInUp 1s ease-out forwards;
          }
          .animation-delay-500 {
            animation-delay: 0.5s;
          }
          .delay-1000 {
            animation-delay: 1s;
          }
          .delay-2000 {
            animation-delay: 2s;
          }

          @keyframes revealText {
            0% {
              clip-path: inset(0 100% 0 0);
            }
            100% {
              clip-path: inset(0 0 0 0);
            }
          }
          .reveal-text {
            animation: revealText 1.5s cubic-bezier(0.77, 0, 0.175, 1) forwards;
          }

          @keyframes circularPulse {
            0% {
              box-shadow: 0 0 0 0 rgba(147, 51, 234, 0.2);
              border-radius: 50%;
              opacity: 0.6;
            }
            70% {
              box-shadow: 0 0 0 300px rgba(147, 51, 234, 0);
              border-radius: 50%;
              opacity: 0;
            }
            100% {
              box-shadow: 0 0 0 0 rgba(147, 51, 234, 0);
              border-radius: 50%;
              opacity: 0;
            }
          }
          .circular-pulse {
            animation: circularPulse 6s cubic-bezier(0.1, 0.5, 0.1, 1) infinite;
          }

          /* Digital counter styles */
          .digital-cell {
            position: relative;
            width: 80px;
            height: 120px;
            margin: 0 5px;
            overflow: hidden;
            background-color: rgba(30, 30, 50, 0.4);
            border-radius: 10px;
            box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5),
              0 0 15px rgba(138, 75, 255, 0.5);
          }

          .digital-number {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 90px;
            font-weight: bold;
            color: rgba(138, 75, 255, 0.7);
            text-shadow: 0 0 10px rgba(138, 75, 255, 0.7);
            animation: digitalShuffle 0.1s linear infinite;
          }

          @keyframes digitalShuffle {
            0%,
            100% {
              transform: translateY(0);
              opacity: 0.2;
            }
            50% {
              transform: translateY(-2px);
              opacity: 0.5;
            }
          }

          .digital-fixed {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 90px;
            font-weight: bold;
            color: rgba(177, 138, 255, 0.9);
            text-shadow: 0 0 15px rgba(177, 138, 255, 0.9);
            animation: digitalGlow 2s ease-in-out infinite;
          }

          @keyframes digitalGlow {
            0%,
            100% {
              text-shadow: 0 0 15px rgba(177, 138, 255, 0.9);
            }
            50% {
              text-shadow: 0 0 25px rgba(217, 70, 239, 0.9),
                0 0 40px rgba(217, 70, 239, 0.5);
            }
          }

          .drop-shadow-glow {
            filter: drop-shadow(0 0 8px rgba(191, 219, 254, 0.3));
          }
        `}</style>
      </div>
    </>
  );
}
